@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500&display=swap);
:root {
    --primary-color: white;
    --secondary-color: black;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    scroll-behavior: smooth;
    height: 100vh;
}

body {
    font-family: 'Montserrat', sans-serif;
    overflow: hidden;
}

a {
    text-decoration: none;
}

.body-container {
    display: grid;
}

/* header section */

header {
    height: 4.5vh;
    min-height: 40px;
    background-color: #1f2833;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    z-index: 9999;
    position: sticky;
    position: -webkit-sticky;
    top: 0;
}

header nav {
    margin-left: auto;
    margin-right: auto;
    width: 40vw;
}

header nav ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    color: white;
    margin-top: 5px;
}

header nav ul li a {
    font-size: 25px;
    list-style: none;
    padding: 2px;
    text-decoration: none;
    border-radius: 3px;
    border: none;
    color: white;
    opacity: 0.5;
}

header nav ul li a:hover {
    border-radius: 3px;
    border: none;
    color: white;
    opacity: 0.3;
    transition: 0.3s;
}

header nav ul li a:active {
    color: black;
    opacity: 0.5;
    transition: 0s;
}

/* hero section */

.hero {
    background: linear-gradient(rgba(250, 250, 250, 0.3),
            rgba(0, 0, 0, 0.8)),
        url(/React-SPA-Portfolio/static/media/contact.8274366f.jpeg);
    width: 100%;
    height: 90vh;
    background-size: cover;
    background-position: center;
    position: relative;
    background-attachment: fixed;
    overflow: hidden;
}

.content-area {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    text-align: center;
}

.content p {
    font-size: 30px;
    color: white;
    color: var(--primary-color);
    text-shadow: 0 0 10px rgba(0, 0, 0, 1);
}

/* landing page */

.landing {
    display: grid;
    background:
        linear-gradient(rgba(0, 0, 0, 0.5),
            rgba(0, 0, 0, 0.7)),
        url(/React-SPA-Portfolio/static/media/jake-hills-mI02K_LxlfU-unsplash.960e5dbc.jpg);
    background-size: cover;
    height: 90vh;
}

.landing-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 30vh;
    margin-bottom: 20vh;
    color: white;
}

.landing-title h1 {
    text-align: center;
    font-weight: 500;
    font-size: 60px;
    margin-bottom: 10px;
    letter-spacing: 5px;
}

.landing-subtitle {
    margin: 3vh;
    color: #90ccf4;
    font-weight: 300;
    text-align: center;
    letter-spacing: 5px;
}

.landing-subtitle span {
    color: #00ffee;
    letter-spacing: 6px;
}

.landing-title a {
    text-decoration: none;
    font-size: 20px;
    font-weight: 200;
    display: flex;
    justify-content: center;
    text-align: center;
    text-decoration: none;
    color: white;
}

.landing-container a:hover {
    opacity: 0.7;
    transition: 0.1s;
}

.landing-container a:active {
    opacity: 0.4;
    transition: 0s;
}

.landing-headline h1 {
    font-size: 40px;
    padding: 50px;
    margin-bottom: 60px;
    color: rgb(54, 43, 214);
}

/* about me section */

/* container div */
.about-me-section {
    display: flex;
    height: 90vh;
    width: 100%;
}

.abtTitle {
    margin-left: 6vw;
}

.about-title {
    color: rgba(37, 37, 37, 0.605);
    font-weight: 200;
    margin-top: 18%;
    font-size: 50px;
}

.about-text {
    color: rgba(250, 130, 2, 0.968);
    margin-top: 20px;
    margin-left: 100px;
    font-weight: 300;
    width: 70%;
    letter-spacing: 2px;
    font-size: 19px;
    line-height: 2;
}

/* left side about me div */
.leftCol {
    height: 100vh;
    width: 60%;
}

/* right side about me div */
.rightCol {
    background-image: url(/React-SPA-Portfolio/static/media/Me.ca56cd63.jpg);
    height: 90vh;
    width: 40%;
    background-position: center;
    background-size: cover;
    position: relative;
}

.blur {
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
    height: 100%;
    width: 100%;
    position: absolute;
    background:
        linear-gradient(rgba(255, 255, 255, 0.03),
            rgba(255, 255, 255, 0.03))
}

.me {
    border-radius: 50%;
    height: 60%;
    width: auto;
    position: absolute;
    left: -70px;
    margin-top: 25%;
}

/* my work/projects section */

.projects {
    height: 90vh;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    background:
        linear-gradient(rgba(0, 0, 0, 0.01),
            rgba(0, 0, 0, 0.1));
}

.card {
    margin-top: 1vh;
    height: 70vh;
    width: 20vw;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.card img {
    height: 225px;
    object-fit: cover;
}

.card-body {
    display: grid;
}

.links {
    display: inline-flex;
    align-self: flex-end;
    justify-content: space-around;
}

.card a {
    width: 45%;
    height: 40px;
}

/* contact me/footer section */

.contact-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    text-align: center;
    list-style: none;
    color: white;
    color: var(--primary-color);
    font-size: 20px;
}

.contact-form {
    height: 80vh;
    width: 40vw;
}

.contact-form form {
    width: 50vw;
}

.contact-section a {
    color: white;
    color: var(--primary-color);
    text-decoration: none;
    padding: 8px;
}

.contact-section a:hover {
    background: white;
    background: var(--primary-color);
    color: black;
    color: var(--secondary-color);
    border-radius: 5px;
    text-shadow: none;
    transition: 0.5s;
}

.contact-section a:active {
    opacity: 0.4;
    transition: 0s;
}

.footerClass {
    height: 6vh;
    color: white;
    text-align: center;
    background-color: rgb(8, 7, 10);
}

.footerClass p {
    margin-top: 1vh;
}

/* resume stuff */

.skills-n-stuff {
    height: 90vh;
}

/* media query */

@media screen and (max-width: 980px) {
    header {
        padding-bottom: 0;
        justify-content: center;
        position: relative;
    }

    body {
        overflow: visible;
    }
}

@media screen and (max-width: 667px) {

    .about-me-text p {
        height: 100%;
    }

    html {
        overflow: scroll;
    }

    ;
}

